<template>
  <div class="login" @keydown.enter="handleSubmit">
    <!-- <div class="login_title">
      <img class="login-title-img" src="../styles/login_logo.png" alt="">
      <p class="login-title-p">OMC-全媒体平台-BO</p>
    </div> -->
    <div class="login-con">
      <!-- <div style="
          display: flex;
          justify-content: flex-end;
          margin-top: 18px;
          margin-right: 18px;
          "
        >
        <img :src="loginSetting" alt="" style="margin-right:18px;">
        <img :src="loginMin" alt="" style="margin-right:18px;">
        <img :src="loginClose" alt="">
      </div> -->
      <div style="display:flex;justify-content:center;margin-top:112px;">
        <img :src="loginLogo" alt="">
      </div>
      <div style="display:flex;justify-content:center;margin-top:29px;font-size:22px;font-family:Microsoft YaHei;font-weight:400;color:rgba(254,254,255,1);">
        <!-- 数字录播系统 -->
        {{$t('login.systemName')}}
      </div>
      <div>
        <Spin fix v-if="loading" size="large" class="login-spin">
          <Icon type="load-c" size="24" class="demo-spin-icon-load"/>
          <div>{{$t('login.logining')}}</div>
        </Spin>
        <!-- <p slot="title">
          <Icon type="log-in" />欢迎登录
        </p> -->
        <div class="form-con">
          <Form ref="loginForm" :model="form" :rules="rules">
            <FormItem prop="userName">
              <Input v-model="form.userName" :placeholder="$t('login.userName')" id="userName">
              </Input>
            </FormItem>
            <FormItem prop="password" style="margin-bottom:40px;">
              <Input type="password" v-model="form.password" :placeholder="$t('login.password')" id="password">
              </Input>
            </FormItem>
            <FormItem>
              <Button @click="handleSubmit" type="primary" long :loading="loading" style="background:rgba(63,153,244,1);border-radius:2px;">{{$t('login.login')}}</Button>
            </FormItem>
          </Form>
          <!--<p class="login-tip">输入任意用户名和密码即可</p>-->
        </div>
      </div>
    </div>
    <Modal v-model="licenseExpiredModal" class-name="licenseExpiredModal" width="500" :mask-closable="false">
      <div style="color:#F56C6C;font-size:14px;">
        <Icon type="md-alert" size="16" />
        {{$t('login.trialVersionExpired')}}
      </div>
      <p slot="footer">
        <Button type="primary" style="background:#3F99F4;border-radius:2px;" @click="licenseExpiredModal=false">{{$t('login.ok')}}</Button>
      </p>    
    </Modal>
    <Modal v-model="licenseExpiredModal1" class-name="licenseExpiredModal" width="500" :mask-closable="false">
      <div style="color:#F56C6C;font-size:14px;">
        <Icon type="md-alert" size="16" />
        {{$t('login.officialAuthorizationExpired')}}
      </div>
      <p slot="footer">
        <Button type="primary" style="background:#3F99F4;border-radius:2px;" @click="licenseExpiredModal1=false">{{$t('login.ok')}}</Button>
      </p>    
    </Modal>
  </div>
</template>

<script>
import api from '@/api/api'
import avatar from '../images/avatar-default.jpg'
import * as types from '@/store/modules/types'
import MenuUtils from '@/libs/MenuUtils'

/** 2020.7.7 UI优化添加 */
import loginSetting from '@/images/login_setting.png';
import loginMin from '@/images/login_min.png';
import loginClose from '@/images/login_close.png';
import loginLogo from '@/images/login_logo.png';
import expiry from '@/images/expiry.png';
// import { set } from 'vue/types/umd'
export default {
  data () {
    return {
      form: {
        userName: '',
        password: ''
      },
      rules: {
        userName: [{ required: true, message: this.$t('login.enterAccount'), trigger: 'blur' }],
        password: [{ required: true, message: this.$t('login.enterPassword'), trigger: 'blur' }]
      },
      loading: false,
      id2index: [],

      loginSetting:loginSetting, // 登录页面 设置 图片
      loginMin:loginMin, // 登录页面 最小化 图片
      loginClose:loginClose, // 登录页面 关闭 图片
      loginLogo:loginLogo, // 登录页面 Logo 图片
      hasChannelManage:false, // 用户是否拥有“通道管理”菜单权限，默认为false，没有该菜单权限
      sumaQtPlayer:'',

      licenseType:'',  // Number  授权类型，0：临时授权，1：永久授权
      softSerialNumber:'',  // 序列号
      licenseCode:'',  // 授权码
      expiryDate:'',  // 到期时间
      licenseStatus:'',  // 授权状态，0：不可用，1：可用
      licenseExpiredModal:false,  // 临时授权过期Modal框
      licenseExpiredModal1:false,  // 正式授权过期Modal框
      secondTimeStamp:'', // Number  服务器时间戳，秒
      serverTime:'', // 服务器时间，YYYY-mm-dd
      days:'', // 距离授权到期剩余天数
      expiry:expiry, // 到期时间提示框中图标

    }
  },
  mounted(){
    console.log('哈哈哈666',this.$sumaQtPlayer)
    setTimeout(() => {
      console.log('哈哈哈777',this.$sumaQtPlayer)
      this.getServerTime();
      if(this.$sumaQtPlayer){
        this.getVersion()
        this.destroyQmlPlayer([{groupIndex:-2,playerIndex:-2}])
      }
    }, 0);
  },
  methods: {
     //获取版本号
    getVersion(){
      let obj={}
      api.getVersion(obj).then(res=>{
        let param = {
          versionCode:0,
          versionName:res.result,
          versionDescription:''
        }
        this.setVersionInfo(param)
      })
    },
     //播放器添加版本号
    setVersionInfo(param){
     this.$sumaQtPlayer.setVersionInfo(JSON.stringify(param),function (callback) {
      });
    },
    //销毁播放器
    destroyQmlPlayer(param){
     this.$sumaQtPlayer.destroyPlayer(JSON.stringify(param),function (callback) {
      });
    },
     //查询所有下拉框数据
    searchMetadata(){
      let param ={}
      api.searchMetadata(param).then(res=>{
        console.log('所有下拉框',res)
        localStorage.setItem('selectAllData',JSON.stringify(res.result))
      })
    },

    /** 获取服务器时间 */
    getServerTime(){
      let param = {}
      api.getServerTime(param).then(
        res => {
          this.secondTimeStamp = res.result.secondTimeStamp;
          this.serverTime = this.formatDate(new Date(this.secondTimeStamp*1000));
          console.log("当前服务器时间", this.serverTime);
          this.getLicenseInfo();
          this.setQmlPlayerServerTime(this.secondTimeStamp)
        }
      )
    },
    //设置服务器时间
    setQmlPlayerServerTime(serverUnixTimestamp) {
        var jsonDemo = {
          serverUnixTimestamp,
        };

        this.$sumaQtPlayer.setServerTime(JSON.stringify(jsonDemo), function (
          callback
        ) {
         
        });
    },
    /** 查询授权状态 */
    getLicenseInfo(){
      let param = {}
      api.getLicenseInfo(param).then(
        res => {
          console.log("授权信息1111111", res);
          this.licenseType = res.result.licenseType;
          this.expiryDate = res.result.expiryDate;
          this.licenseStatus = res.result.licenseStatus;
        }
      )
    },

    /** 时间戳转换为日期 */
    formatDate(now) {
      var year = now.getFullYear();
      var month = now.getMonth() + 1;
      var date = now.getDate();
      // var hour = now.getHours();
      // var minute = now.getMinutes();
      // var second = now.getSeconds();
      // return year + "-" + month + "-" + date + " " + hour + ":" + minute + ":" + second;
      return year + "-" + month + "-" + date;
    },


    /** 日期格式化 */
    dateFormat(fmt, date) {
      let ret;
      const opt = {
        "Y+": date.getFullYear().toString(),        // 年
        "m+": (date.getMonth() + 1).toString(),     // 月
        "d+": date.getDate().toString(),            // 日
        "H+": date.getHours().toString(),           // 时
        "M+": date.getMinutes().toString(),         // 分
        "S+": date.getSeconds().toString()          // 秒
        // 有其他格式化字符需求可以继续添加，必须转化成字符串
      }
      for (let k in opt) {
        ret = new RegExp("(" + k + ")").exec(fmt);
        if (ret) {
          fmt = fmt.replace(ret[1], (ret[1].length == 1) ? (opt[k]) : (opt[k].padStart(ret[1].length, "0")))
        }
      }
      return fmt;
    },

    /** 计算2个日期之间的天数之差 */
    getDays(strDateStart, strDateEnd){
      var strSeparator = "-"; //日期分隔符
      var oDate1;
      var oDate2;
      var iDays;
      oDate1= strDateStart.split(strSeparator);
      oDate2= strDateEnd.split(strSeparator);
      var strDateS = new Date(oDate1[0] + "-" + oDate1[1] + "-" + oDate1[2]);
      var strDateE = new Date(oDate2[0] + "-" + oDate2[1] + "-" + oDate2[2]);
      // iDays = parseInt(Math.abs(strDateS - strDateE ) / 1000 / 60 / 60 /24);//把相差的毫秒数转换为天数
      iDays = parseInt((strDateE - strDateS) / 1000 / 60 / 60 /24);//把相差的毫秒数转换为天数

      return iDays ;
    },

    handleSubmit () {
      // this.licenseToLogin();
      // 根据授权状态判断是否可以登录
      this.days = this.getDays(this.serverTime, this.expiryDate);
      if (this.licenseType == 0) {
        if (this.days <= 0) {
          this.licenseExpiredModal = true;
          return false;
        }
      }
    
      // 正式授权后，到期了，也不能登录 2020年10月12日 与组长确认
      if (this.licenseType == 1) {
        if (this.days <= 0) {
          this.licenseExpiredModal1 = true;
          return false;
        }
      }
      
      // 对字符串进行编码
      var encode = encodeURI(this.form.password);
        // 对编码的字符串转化base64
      var base64 = btoa(encode);
      this.$refs.loginForm.validate(valid => {
        if (valid) {
          let param = {
              userName: this.form.userName,
              // 'password': crypto.createHash('md5').update(this.form.password, 'utf8').digest('hex'),
              password: base64,
              regionCode: '',
              systemType:'2'
          }
          this.loading = true
          api.requestLogin(param).then(
            res => {
              let oldId = null
              try {
                oldId = this.$store.state.user.info.user.id
              } catch (error) {
                oldId = null
              }

              // TODO 登录后组装需要添加的路由
              let tempArr = []
              this.searchMetadata()
              // console.log('登录结果：', res)
              MenuUtils(tempArr, res.user.permissions)
              // this.$router.addRoutes(tempArr)
              // console.log('MenuUtils处理：', tempArr)
              // 根据id和parentId进行组装
              for (let i = 0; i < tempArr.length; i++) {
                this.id2index[tempArr[i].id] = i
              }
                    let finalRouter = []
              for (let item of tempArr) {
                if (item) {
                  if (item.parentId !== '0') {
                  // 通过item的parentId进行children的拼装
                    if (tempArr[this.id2index[item.parentId]] !== undefined && !Object.prototype.hasOwnProperty.call(tempArr[this.id2index[item.parentId]],'children')) {
                      // item.children = []
                      tempArr[this.id2index[item.parentId]].children = []
                    }
                    if (tempArr[this.id2index[item.id]].id && tempArr[this.id2index[item.parentId]] !== undefined) {
                      tempArr[this.id2index[item.parentId]].children.push(tempArr[this.id2index[item.id]])
                    }
                  } else {
                    finalRouter.push(tempArr[this.id2index[item.id]])
                  }
                }
              }
              for (let item of finalRouter) {
                if (item.children) {
                  // for (let citem of item.children) {
                  //   this.$store.state.app.tagsList.push(citem)
                  // }
                  this.$store.state.app.tagsList.push(...item.children)
                }
              }
              // console.log('login', this.$store.state.app.tagsList)
              this.$router.addRoutes(finalRouter)
              console.log("router", finalRouter)

              // for (let item of finalRouter) {
              //   if (item.title == '录播管理') {
              //     if (item.children && item.children.length > 0) {
              //       for (let item1 of item.children) {
              //         if (item1.title == '通道管理') {
              //           this.hasChannelManage = true; 
              //           localStorage.setItem('hasChannelManage', this.hasChannelManage);
              //           break;
              //         }
              //       }
              //     }
              //   }
              // }

              // 如果新登录的用户和之前的用户不是一个，则清空之前打开的tab页
              if (this.form.userName !== oldId) {
                this.$store.commit('clearAllTags')
              }
              this.$store.commit(types.LOGIN, res)
              this.$store.commit(
                'setAvator',
                res.user.avatar ? res.user.avatar : avatar
              )
              this.$Message.success(this.$t('login.loginSuccess'))

              // 提示用户剩余天数
              if(this.licenseType == 0) {
                this.$Modal.info({
                  title: "",
                  content:
                    `<div style='color:#02E8FF'>
                      <span>${this.$t('login.trialVersionRemainDays1')} ${this.days} ${this.$t('login.trialVersionRemainDays2')}</span>
                    </div>`,
                });
              } else if (this.licenseType == 1) {
                if (this.days <= 30) {  // 2020年10月10日 和产品经理确认，永久授权到期时间剩余时间为30天时，也要弹框提醒。
                  this.$Modal.info({
                    title: "",
                    content:
                      `<div style='color:#02E8FF'>
                        <span>${this.$t('login.officialAuthorizationRemainDays1')} ${this.days} ${this.$t('login.officialAuthorizationRemainDays2')}</span>
                       </div>`,
                  });
                }
              }

              localStorage.setItem('loginRes', JSON.stringify(res))
              this.$bus.$emit('loginStatus',res)
              // TODO
              // console.log(this.$store.state.app.routers)

              for (let item of finalRouter) {
                // console.log("菜单",item.title);
                if (item.title == '系统管理') {
                  if (item.children && item.children.length > 0) {
                    for (let item1 of item.children) {
                      if (item1.title == '角色管理') {
                        // this.$router.push({
                        //   path:'/system-manage/system-manage-role'
                        // })
                        localStorage.setItem('firstPage','/system-manage/system-manage-role')
                        break;
                      }
                      if (item1.title == '操作员管理') {
                        // this.$router.push({
                        //   path:'/system-manage/system-manage-admin'
                        // })
                        localStorage.setItem('firstPage','/system-manage/system-manage-admin')
                        break;
                      } 
                    }
                  }
                  break
                }else if (item.title=='录播管理') {
                  if (item.children && item.children.length > 0) {
                    for (let item1 of item.children) {
                      if (item1.title == '通道管理') {
                        // this.$router.push({
                        //   path:'/base-manage/recode-manage-channel'
                        // })
                        localStorage.setItem('firstPage','/base-manage/recode-manage-channel')
                        break;
                      }
                      if (item1.title == '文件管理') {
                        // this.$router.push({
                        //   path:'/base-manage/recode-manage-file'
                        // })
                        localStorage.setItem('firstPage','/base-manage/recode-manage-file')
                        break;
                      }
                      if (item1.title == '播放中心') {
                        // this.$router.push({
                        //   path:'/base-manage/recode-manage-play'
                        // })
                        localStorage.setItem('firstPage','/base-manage/recode-manage-play')
                        break;
                      }
                    }
                  }
                  break;
                }else if (item.title=='运维管理') {
                  if (item.children && item.children.length > 0) {
                    for (let item1 of item.children) {
                      if (item1.title == '系统日志') {
                        // this.$router.push({
                        //   path:'/operation-manage/operation-manage-log'
                        // })
                        localStorage.setItem('firstPage','/operation-manage/operation-manage-log')
                        break;
                      }
                      if (item1.title == '服务器信息') {
                        // this.$router.push({
                        //   path:'/operation-manage/operation-manage-server'
                        // })
                        localStorage.setItem('firstPage','/operation-manage/operation-manage-server')
                        break;
                      }
                      if (item1.title == 'CDN业务监控') {
                        // this.$router.push({
                        //   path:'/operation-manage/operation-manage-cdn'
                        // })
                        localStorage.setItem('firstPage','/operation-manage/operation-manage-cdn')
                        break;
                      }
                    }
                  }
                }
              }
              /**判断是否有通道管理页面 */
              sessionStorage.setItem('hasChnanel',false)
              for (let item of finalRouter) {
                 if (item.title=='录播管理') {
                  if (item.children && item.children.length > 0) {
                    for (let item1 of item.children) {
                      if (item1.title == '通道管理') {
                        sessionStorage.setItem('hasChnanel',true)
                        break;
                      }
                    }
                  }
                }
              }
              // this.$router.push({
              //   path: '/'
              // })
              // 储存动态菜单数据
              let menuArray = []
              for (let item of res.user.permissions) {
                // 弹出的Tab标签页 需要通过对上级Tab标签页的菜单权限进行判断后push
                if (item) {
                  if(item.name=='通道管理'){
                    menuArray.push('通道监控')
                  }
                  menuArray.push(item.name)
                }
                if(item) {
                  if(item.name=='系统管理'){
                    menuArray.push('新建操作员')
                  }
                  menuArray.push(item.name)
                }
                if(item) {



                  if(item.name=='系统管理'){
                    menuArray.push('修改操作员')
                  }
                  menuArray.push(item.name)
                }
                if(item) {
                  if(item.name=='系统管理'){
                    menuArray.push('新建角色')
                  }
                  menuArray.push(item.name)
                }
                if(item) {
                  if(item.name=='系统管理'){
                    menuArray.push('修改角色')
                  }
                  menuArray.push(item.name)
                }
                if(item) {
                  if(item.name=='运维管理'){
                    menuArray.push('服务器信息')
                  }
                  menuArray.push(item.name)
                }
              }
              menuArray.push('个人中心')
              localStorage.setItem('menuDataList', JSON.stringify(menuArray))
              this.$router.push({
                path: localStorage.getItem('firstPage')
              })
              this.loading = false;
            },
            err => {
              this.loading = false
              // this.$Message.error('登陆失败' + err)
            }
          )
        }
      })
    }
  }
}
</script>
<style>

</style>

